.banner .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background: transparent;
  border: 1px solid #fff;
  opacity: 1;
  transition: 0.4s ease;
}
.banner .swiper-pagination-bullet-active {
  width: 24px;
  background-color: #fff;
}
.banner .swiper-button-next:after, .banner .swiper-button-prev:after {
  font-size: 37px;
  color: #d60e00;
}
.banner .swiper-button-prev {
  left: 4%;
  right: auto;
}
.banner .swiper-button-next {
  right: 4%;
}
.banner__grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  height: 60vh;
  min-height: 550px;
}
.banner__categories {
  margin-bottom: 0px;
  list-style: none;
  padding: 0px;
}
.banner__categories-item {
  position: relative;
  display: inline-block;
  margin: 0px 8px 8px 0px;
}
.banner__categories-item::before {
  content: ",";
  position: absolute;
  right: -2px;
  top: 0;
}
.banner__categories-link {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  letter-spacing: 1.5px;
}
.banner__title {
  font-size: 64px;
  font-weight: 700;
  margin-bottom: 18px;
  color: #404040;
}
.banner__post-author {
  list-style: none;
  margin: 0px;
  padding: 0px;
  margin-bottom: 10px;
}
.banner__post-author-item {
  display: inline-block;
  margin-right: 16px;
}
.banner__post-author-link {
  font-size: 12px;
}
.banner__description {
  display: block; /* Fallback for non-webkit */
  display: -webkit-box;
  font-size: 24px;
  line-height: 1.5;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 24px;
  color: #404040;
}
.banner__info-container {
  grid-column: 2/7;
  grid-row: 1;
  position: relative;
  z-index: 1;
}
.banner__image-container {
  height: 100%;
  min-height: 550px;
  grid-column: 6/-1;
  grid-row: 1;
}
.banner__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner__read-more {
  position: relative;
  color: #d60e00;
}
.banner__read-more::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 40%;
  height: 2px;
  background-color: #d60e00;
  transition: 0.4s ease;
}
.banner__read-more:hover::before {
  right: 0;
}
@media only screen and (max-width: 1399.98px) {
  .banner__title {
    font-size: 42px;
  }
}
@media only screen and (max-width: 1199.98px) {
  .banner__title {
    font-size: 37px;
  }
}
@media only screen and (max-width: 991.98px) {
  .banner__info-container {
    grid-column: 2/8;
  }
}
@media only screen and (max-width: 767.98px) {
  .banner .swiper-button-next:after,
.banner .swiper-button-prev:after {
    font-size: 32px;
  }
  .banner__grid {
    height: 100vh;
  }
  .banner__info-container {
    grid-column: 2/12;
    padding-top: 0px;
  }
  .banner__image-container {
    grid-column: 1/13;
    grid-row: 1;
    background-color: #000000;
  }
  .banner__image {
    opacity: 0.2;
  }
  .banner__info-container {
    grid-column: 2/12;
  }
  .banner__title {
    color: #fff;
    font-size: 24px;
  }
  .banner__post-author-link {
    color: #fff;
  }
  .banner__description {
    color: #fff;
    font-size: 24px;
  }
  .banner__categories-link {
    color: #fff;
    font-size: 12px;
  }
}
@media only screen and (max-width: 420px) {
  .banner__title {
    font-size: 20px;
  }
  .banner__description {
    font-size: 14px;
  }
  .banner__categories-link {
    font-size: 10px;
  }
}

.welcome {
  position: relative;
  width: 100%;
  z-index: 1;
}
.welcome__image-container {
  position: relative;
  width: 100%;
  display: block;
}
.welcome__image-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.welcome__image-container:before {
  content: "";
  display: block;
  padding-top: 56.2002275313%;
}
.welcome .container {
  position: relative;
  z-index: 5;
}
.welcome__vector-container {
  width: 100%;
  position: absolute;
  bottom: calc(100% - 80px);
}
.welcome__vector {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.welcome__slider {
  border-radius: 37px;
  box-shadow: 0px 14px 48px -6px rgba(0, 0, 0, 0.21);
}
.welcome__media-container {
  border-radius: 37px;
  border: 24px solid #fff;
  overflow: hidden;
}
.welcome__image-container {
  border-radius: 17px;
  overflow: hidden;
}
@media only screen and (max-width: 1399.98px) {
  .welcome__vector-container {
    bottom: calc(100% - 60px);
  }
  .welcome__media-container {
    border: 22px solid #fff;
  }
  .welcome__slider {
    border-radius: 32px;
  }
}
@media only screen and (max-width: 1199.98px) {
  .welcome__vector-container {
    bottom: calc(100% - 55px);
  }
  .welcome__media-container {
    border: 20px solid #fff;
  }
  .welcome__slider {
    border-radius: 27px;
  }
}
@media only screen and (max-width: 991.98px) {
  .welcome__vector-container {
    bottom: calc(100% - 42px);
  }
  .welcome__media-container {
    border: 18px solid #fff;
  }
  .welcome__slider {
    border-radius: 24px;
  }
}
@media only screen and (max-width: 767.98px) {
  .welcome__vector-container {
    bottom: calc(100% - 60px);
  }
  .welcome__media-container {
    border: 16px solid #fff;
  }
}
@media only screen and (max-width: 575.98px) {
  .welcome {
    padding-top: 32px;
  }
  .welcome__media-container {
    border: 14px solid #fff;
  }
}
@media only screen and (max-width: 480px) {
  .welcome__vector-container {
    bottom: calc(100% - 32px);
  }
}

.card-programstudi {
