.card-testimonial {
  position: relative;
}
.card-testimonial__user-image-container {
  position: relative;
  width: 100%;
  display: inline-block;
  max-width: 64px;
}
.card-testimonial__user-image-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.card-testimonial__user-image-container:before {
  content: "";
  display: block;
  padding-top: 100%;
}
.card-testimonial__msg-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 24px;
  border: 1px solid #EDEDED;
  min-height: 250px;
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
